<template>
    <div class="board">
        <div v-for="i in 8" :key="i" class="row">
            <BoardTile v-for="j in 8" :key="i + j" :row="(pov === 1 ? i : 8 - i)" :column="j" :piece="checkPiece(j, i)"></BoardTile>
        </div>
    </div>
</template>

<script>
    import BoardTile from './BoardTile.vue';
    import FENReader from '../FENReader.js';

    export default {
        name: 'Board',
        props: {
            pov: Number,
        },
        components: {
            BoardTile,
        },
        data() {
            return {
                whiteToPlay: true,
                pieces: [ ],
            }
        },
        methods: {
            checkPiece(x, y) {
                let foundPieces = this.pieces.filter(item => {
                    return item.positionX === x && item.positionY === y;
                });

                if (foundPieces.length > 0) return foundPieces[0];
                else return null;
            }
        },
        created() {
            let reader = new FENReader();
            this.pieces = reader.convert('5r2/2p2rb1/1pNp4/p2Pp1pk/2P1K3/PP3PP1/5R2/5R2');
        },
    };
</script>

<style scoped lang="scss">
    .row {
        clear: both;
    }
</style>

